.gutter-30 > li, .gutter-30 > div {
  padding-top: 15px !important;
  padding-bottom: 15px !important;
}

.mb--30 {
  margin-bottom: 30px;
}

/*==== text color & bg color ====*/
/*============================ 
// Global styles
=============================*/
html {
  overflow-x: hidden !important;
  width: 100%;
  height: 100%;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
  vertical-align: baseline;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  position: relative;
  background-color: var(--color-white);
  transition: all 0.3s ease-in-out;
  z-index: 2;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: var(--color-primary);
  display: inline-block;
  transition: all 0.3s ease-in-out;
}
a:link, a:visited, a:focus {
  text-decoration: none;
  outline: 0;
}
a:hover {
  text-decoration: none;
  color: var(--color-primary);
}

img {
  max-width: 100%;
  height: auto;
}

input:focus,
textarea:focus,
select:focus {
  outline: 0;
  box-shadow: none;
}

.no-padding {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.no-padding__left {
  padding-left: 0 !important;
}
.no-padding__right {
  padding-right: 0 !important;
}

.section-padding {
  padding-block: 140px;
}
@media (max-width: 1199px) {
  .section-padding {
    padding: 100px 0;
  }
}
@media (max-width: 991px) {
  .section-padding {
    padding: 80px 0;
  }
}

section,
.section {
  position: relative;
}

@media (min-width: 1400px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl {
    max-width: 1345px;
  }
}
/**
 * Utility CSS.
 */
/*======================================
// pre-loader
=====================================*/
#preloader {
  background: url("../../media/preloader.gif") rgba(255, 255, 255, 0.99) no-repeat scroll center center;
  height: 100%;
  left: 0;
  overflow: visible;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999999;
}

/*======================================
// Back-to-top
=====================================*/
button.scroll-to-top {
  background-color: var(--color-primary);
  height: 0px;
  width: 0px;
  overflow: hidden;
  border-radius: 50px;
  color: transparent;
  clear: both;
  visibility: hidden;
  position: fixed;
  cursor: pointer;
  display: block;
  border: none;
  right: 50px;
  bottom: 75px;
  outline: 0 !important;
  z-index: 99;
  transition: all 0.3s ease-in-out;
}
button.scroll-to-top i {
  color: var(--color-white);
}

button.scroll-to-top.show {
  display: block;
  background-color: var(--color-primary);
  right: 25px;
  bottom: 50px;
  height: 40px;
  width: 40px;
  visibility: visible;
  box-shadow: 6px 6px 12px rgba(181, 98, 0, 0.16);
}
button.scroll-to-top.show:hover i::before {
  animation: toBottomFromTop 0.7s forwards;
}
button.scroll-to-top.show i {
  color: var(--color-white);
}

button.scroll-to-top.show:active {
  box-shadow: 6px 6px 12px rgba(181, 98, 0, 0.16);
}

.rt-icon::before {
  width: auto;
  margin-left: 0;
  margin-right: 0;
  line-height: unset;
}

.rt-bg-gray {
  background-color: var(--color-gray-body);
}

.item-social {
  position: relative;
  z-index: 1;
}
.item-social ul {
  gap: 10px;
}
.item-social ul li a {
  height: 38px;
  width: 38px;
  border-radius: 38px;
  background-color: #1B202C;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease-in-out;
  overflow: hidden;
}
.item-social ul li a svg {
  color: #9E9E9E;
}
.item-social ul li a:hover {
  background-color: var(--color-primary);
  border-color: var(--color-primary);
}
.item-social ul li a:hover svg {
  color: var(--color-white);
  animation: toBottomFromTop 0.7s forwards;
}
.item-social ul li.text {
  color: #F7F6F6;
  font-size: 15px;
  font-weight: 500;
  margin-right: 10px;
}

/**
 * Components: Input.
 */
.input-group {
  width: 100%;
  position: relative;
  display: block;
}
@media only screen and (min-width: 321px) {
  .input-group {
    display: flex;
  }
}
.input-group .form-control {
  border-radius: 100px !important;
  border: 1px solid #414141;
  background-color: transparent;
  height: 60px;
  width: 100%;
  font-size: 16px;
  line-height: 28px;
  color: var(--color-white);
  padding: 0 0 0 20px;
}
.input-group .form-control:focus {
  outline: 0;
  box-shadow: none;
  border-color: var(--color-white);
}
.input-group .form-control::placeholder {
  color: var(--color-gray-font);
}
.input-group .btn-outline-secondary {
  border: 0;
  color: var(--color-primary);
  background-color: var(--color-primary);
  border-radius: 50px !important;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  width: 50px;
  transition: all 0.4s ease-in-out;
  z-index: 4;
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
}
.input-group .btn-outline-secondary svg {
  color: var(--color-white);
  transition: all 0.4s ease-in-out;
}
.input-group .btn-outline-secondary:hover {
  background-color: var(--color-white);
  color: var(--color-black);
}
.input-group .btn-outline-secondary:hover svg {
  animation: headShake 1.5s;
  color: var(--color-primary);
}
.input-group .btn-outline-secondary:focus {
  outline: 0;
  box-shadow: none;
}

.rt-brand-wrap {
  padding-top: 95px;
}
.rt-brand-wrap .rt-blur-shape ul li {
  height: 484px;
  width: 484px;
  border-radius: 484px;
  filter: blur(100px);
  background-color: transparent;
}
.rt-brand-wrap .rt-blur-shape ul li:nth-child(1) {
  background-color: #E8FAF7;
  top: -10%;
  left: -15%;
}
.rt-brand-wrap .rt-blur-shape ul li:nth-child(2) {
  background-color: #FAF8E8;
  top: -10%;
  right: -15%;
  left: auto;
}

.upper-text {
  color: var(--color-gray-font);
  font-size: 18px;
  font-weight: 500;
  line-height: 16px;
  margin-bottom: 45px;
}
.upper-text span {
  color: var(--color-black);
  font-weight: 700;
}

.rt-brand-box {
  cursor: pointer;
}
.rt-brand-box:hover .rt-brand-img img {
  filter: grayscale(0);
  opacity: 1;
}

.rt-brand-img img {
  transition: all 0.4s ease-in-out;
  filter: grayscale(100%);
  opacity: 50%;
}

/**
 * Components: Blog.
 */
.rt-blog-wrap .section-heading .section-shape {
  bottom: 0;
  right: 0;
  animation-duration: 10s;
}
.rt-blog-wrap .stroke-text {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -10%;
}

.rt-blog-box:hover .item-img a img {
  transform: scale(1.05);
}
.rt-blog-box .item-img {
  position: relative;
  border-radius: var(--border-radius-xl);
  z-index: 1;
}
.rt-blog-box .item-img a img {
  border-radius: var(--border-radius-xl);
  transition: all 0.5s cubic-bezier(0.77, 0, 0.175, 1);
}
.rt-blog-box.style-2 .item-img {
  position: relative;
  z-index: 1;
}
.rt-blog-box.style-2 .item-img::before {
  content: "";
  height: 100%;
  width: 100%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.9) 100%);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  border-radius: var(--border-radius-xl);
}
.rt-blog-box.style-2 .item-img .item-content {
  position: absolute;
  bottom: 40px;
  left: 34px;
  z-index: 2;
}
.rt-blog-box.style-2 .item-img .item-content .rt-title {
  margin-bottom: 10px;
}
.rt-blog-box.style-2 .item-img .item-content .rt-title a {
  color: var(--color-white);
}
.rt-blog-box.style-2 .item-img .item-content .entry-meta-list a {
  color: #CBCBCB;
}
.rt-blog-box.style-2 .item-img .item-content .entry-meta-list a svg {
  color: #CBCBCB;
}

.rt-date {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 66px;
  width: 66px;
  background-color: var(--color-primary);
  flex-direction: column;
  border-radius: 13px;
  border: 1px solid #002784;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.1);
  position: absolute;
  bottom: 0;
  left: 0;
}
.rt-date .rt-number {
  font-size: 30px;
  font-weight: 800;
  line-height: normal;
  letter-spacing: 1.8px;
  text-transform: uppercase;
  color: var(--color-white);
}
.rt-date .rt-text {
  font-size: 16px;
  font-weight: 500;
  letter-spacing: inherit;
}

.rt-title {
  font-size: 24px;
  font-weight: 700;
  line-height: 32px;
  text-transform: capitalize;
  margin-bottom: 18px;
}
.rt-title a {
  color: var(--color-black);
  transition: all 0.4s ease-in-out;
}
.rt-title a:hover {
  color: var(--color-primary);
}

.rt-tag .tag {
  color: #FFF;
  font-size: 14px;
  font-weight: 600;
  line-height: 16px;
  background-color: var(--color-primary);
  border-radius: 100px;
  display: inline-block;
  padding: 8px 14px;
}

.entry-meta-list {
  gap: 25px;
}
.entry-meta-list a {
  color: #343C4D;
  transition: all 0.3s ease-in-out;
}
.entry-meta-list a svg {
  color: var(--color-black);
  margin-right: 5px;
  transition: all 0.3s ease-in-out;
}
.entry-meta-list a:hover {
  color: var(--color-primary);
}
.entry-meta-list a:hover svg {
  color: var(--color-primary);
}

.quixa-button .quixa-btn {
  background-color: var(--color-primary);
  font-size: 16px;
  padding: 13px 30px 12px;
  font-weight: 600;
  line-height: normal;
  text-transform: capitalize;
  text-align: center;
  display: inline-block;
  transition: all 0.4s ease;
  position: relative;
  overflow: hidden;
  z-index: 0;
  border: 0;
  border-radius: var(--border-radius-sm);
}
.quixa-button .quixa-btn.style-2 {
  background-color: var(--color-secondary);
  border-radius: 100px;
  border: 2px solid var(--color-black);
  text-transform: uppercase;
  font-weight: 800;
  box-shadow: 4px 7px 0px 0px rgba(0, 0, 0, 0.2);
  padding: 18px 30px;
}
.quixa-button .quixa-btn.style-2 span svg {
  color: var(--color-black);
}
.quixa-button .quixa-btn.style-2:hover {
  background-color: var(--color-secondary);
  border-color: var(--color-black);
  box-shadow: 4px 7px 0px 0px rgba(0, 0, 0, 0.2);
}
.quixa-button .quixa-btn.style-2:hover span svg {
  color: var(--color-black);
}
.quixa-button .quixa-btn.style-2 .btn-wrap .btn-text1 {
  color: var(--color-black);
}
.quixa-button .quixa-btn.style-2 .btn-wrap .btn-text2 {
  color: var(--color-black);
}
.quixa-button .quixa-btn.style-3 {
  background-color: var(--color-white);
  border-radius: 100px;
}
.quixa-button .quixa-btn.style-3 .btn-wrap .btn-text1 {
  color: var(--color-black);
}
.quixa-button .quixa-btn.style-3 .btn-wrap .btn-text1 svg {
  color: var(--color-black);
}
.quixa-button .quixa-btn.style-3 .btn-wrap .btn-text2 {
  color: var(--color-black);
}
.quixa-button .quixa-btn.style-3 .btn-wrap .btn-text2 svg {
  color: var(--color-black);
}
.quixa-button .quixa-btn.style-4 {
  border-radius: 100px;
  border: 1px solid var(--color-blue-600);
  background: linear-gradient(180deg, var(--color-blue-400) -8.33%, var(--color-blue-700) 100%);
  box-shadow: 0px 22px 30px 0px rgba(0, 55, 186, 0.17);
  padding: 13px 24px;
}
.quixa-button .quixa-btn.style-4 span svg {
  color: var(--color-white);
}
.quixa-button .quixa-btn.style-4 .btn-wrap .btn-text1 {
  color: var(--color-white);
}
.quixa-button .quixa-btn.style-4 .btn-wrap .btn-text2 {
  color: var(--color-white);
}
.quixa-button .quixa-btn span svg {
  color: var(--color-white);
  margin-left: 10px;
  transition: all 0.4s ease;
  position: relative;
  top: -2px;
}
.quixa-button .quixa-btn:hover .btn-wrap .btn-text1 {
  transform: translateY(-150%);
}
.quixa-button .quixa-btn:hover .btn-wrap .btn-text2 {
  top: 50%;
  transform: translateY(-50%);
}
.quixa-button .quixa-btn .btn-wrap {
  display: inline-block;
  overflow: hidden;
  position: relative;
  z-index: 1;
  float: left;
}
.quixa-button .quixa-btn .btn-wrap .btn-text1 {
  color: var(--color-white);
  display: block;
  position: relative;
  transition: all 0.4s ease-in-out;
}
.quixa-button .quixa-btn .btn-wrap .btn-text2 {
  position: absolute;
  top: 100%;
  display: block;
  color: var(--color-white);
  transition: all 0.4s ease-in-out;
}

.arrow-button .arrow-btn {
  border-radius: 13px;
  border: 1px solid #BDBDBD;
  background-color: var(--color-white);
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.1);
  height: 66px;
  width: 66px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
}
.arrow-button .arrow-btn svg {
  color: var(--color-black);
}
.arrow-button .arrow-btn:hover svg {
  animation: headShake 1s ease;
}

.item-button .item-btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  color: var(--color-white);
  padding: 13px 24px;
  border: none;
  border-radius: 100px;
  transition: all 0.3s ease-in-out;
  z-index: 1;
  padding-left: 22px;
}
.item-button .item-btn:hover {
  padding-right: 50px;
}
.item-button .item-btn:hover span {
  visibility: visible;
  opacity: 1;
}
.item-button .item-btn:hover::after {
  width: 100%;
  border-radius: 100px;
  border: 1px solid var(--color-blue-600);
  background: linear-gradient(180deg, var(--color-blue-400) -8.33%, var(--color-blue-700) 100%);
  box-shadow: 0px 22px 30px 0px rgba(0, 55, 186, 0.17);
}
.item-button .item-btn:hover svg {
  left: calc(100% - 30px);
}
.item-button .item-btn::after {
  content: "";
  width: 50%;
  height: 100%;
  border-radius: 100px;
  border: 1px solid var(--color-blue-600);
  background: linear-gradient(180deg, var(--color-blue-400) -8.33%, var(--color-blue-700) 100%);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
  z-index: -1;
  transition: all 0.3s ease-in-out;
}
.item-button .item-btn span {
  color: #fff;
  font-weight: 700;
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}
.item-button .item-btn svg {
  color: var(--color-white);
  visibility: visible;
  opacity: 1;
  position: absolute;
  transition: 0.5s;
  left: 50%;
  top: 50%;
  transform: translateY(-50%) translateX(-50%);
}

.button-navigation {
  display: none;
}
@media only screen and (min-width: 768px) {
  .button-navigation {
    display: block;
  }
}
.button-navigation .swiper-button-next, .button-navigation .swiper-button-prev {
  top: 50%;
  transition: all 0.3s ease-out;
  height: 60px;
  width: 60px;
