  transition: all 0.5s cubic-bezier(0.17, 0.67, 0.32, 0.87);
}
.counterUp:before {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  color: var(--color-primary);
  white-space: nowrap;
  content: attr(data-hover);
  transition: all 0.5s cubic-bezier(0.17, 0.67, 0.32, 0.87);
}

.post-fix {
  text-transform: uppercase;
  display: inline-block;
  position: absolute;
  color: #fff;
  font-size: 130px;
  font-weight: 900;
  cursor: pointer;
  text-decoration: none;
  -webkit-text-stroke: 2px #565656;
  color: transparent;
  transition: all 0.5s cubic-bezier(0.17, 0.67, 0.32, 0.87);
}
.post-fix:before {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  color: var(--color-primary);
  white-space: nowrap;
  content: attr(data-hover);
