  font-weight: 600;
  font-size: 16px;
  color: var(--color-black);
  transition: all 0.4s ease-in-out;
}
.rt-main-menu .menu .menu-item .menu-link .shape {
  margin-left: 5px;
}
.rt-main-menu .menu .menu-item .menu-link .shape svg {
  color: var(--color-gray-font);
  transition: all 0.5s ease-in-out;
}
.rt-main-menu .menu .menu-item .menu-link::before {
  height: 3px;
  width: 0;
  content: "";
  background-color: var(--color-primary);
  position: absolute;
  bottom: -2px;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  opacity: 0;
  transition: all 0.4s ease-in-out;
}
.rt-main-menu .menu .menu-item .menu-link:hover {
  color: var(--color-primary);
}
.rt-main-menu .menu .menu-item .menu-link:hover::before {
  opacity: 1;
  width: 100%;
}
.rt-main-menu .menu .menu-item .menu-link:hover .shape svg {
  color: var(--color-primary);
  transform: rotateY(180deg);
}
.rt-main-menu.style-2 .menu .menu-item .menu-link {
  color: var(--color-white);
}
.rt-main-menu.style-2 .menu .menu-item .menu-link .shape svg {
  color: var(--color-white);
}

.rt-main-menu .menu .menu-item:hover.menu-item-has-children::after {
  border-color: var(--color-primary);
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu {
  position: absolute;
  top: 100%;
  background: #ffffff;
  min-width: 260px;
  padding: 15px 12px 13px 12px;
  list-style: none;
  margin: 0;
  box-shadow: 0 13px 48px 0 rgba(0, 0, 0, 0.15);
  visibility: hidden;
  opacity: 0;
  z-index: 9;
  transform-origin: top center;
  transform: scaleY(0) translateZ(100px);
  transition: opacity 500ms ease, visibility 500ms ease, transform 700ms ease;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .menu-item {
  margin-bottom: 0px;
  opacity: 0;
  transition: all 0.4s;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu {
  font-size: 15px;
  padding: 6px 17px;
  color: var(--color-black);
  font-weight: 600;
  overflow: hidden;
  font-family: var(--font-title);
  position: relative;
  z-index: 1;
  display: flex;
  line-height: 30px;
  transition: all 0.4s ease;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu::before {
  color: var(--color-primary);
  content: "\f111";
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  font-size: 7px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0px;
  transition: all 0.4s ease;
  visibility: hidden;
  opacity: 0;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu:hover::before {
  visibility: visible;
  opacity: 1;
  left: 5px;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .child-menu:hover {
  color: var(--color-primary);
  padding-left: 20px;
}

.rt-main-menu .menu .menu-item:hover > .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: scaleY(1) translateZ(0px);
}

.rt-main-menu .menu .menu-item:hover > .sub-menu .menu-item {
  opacity: 1;
}

.rt-main-menu .menu > .menu-item {
  margin-right: 32px;
  line-height: 36px;
}

.rt-main-menu .menu > .menu-item > a {
  padding: 25px 0;
}

.rt-main-menu .menu > .menu-item:last-child {
  margin-right: 0;
}

