}

.offscreen-navigation .sub-menu li.menu-item-has-children > a::after,
.offscreen-navigation .sub-menu li.menu-item-has-children > a::before {
  right: 40px;
}

.offscreen-navigation .sub-menu li a {
  border-top: 1px solid #f1f1f1;
