  transition: all 0.3s ease-in-out;
  font-size: 15px;
  padding: 6px 17px;
  color: var(--color-black);
  font-weight: 500;
  overflow: hidden;
  position: relative;
  z-index: 1;
  display: flex;
  line-height: 30px;
  transition: 500ms;
}

.offcanvas-content-style-2 .offscreen-navigation .sub-menu li a {
  padding-left: 30px;
}

.offscreen-navigation .sub-menu li .sub-menu li a {
  padding: 7px 50px;
}

.offscreen-navigation ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.offscreen-navigation ul li > a {
  font-size: 16px;
  padding: 6px 17px;
  color: var(--color-black);
  font-weight: 600;
  overflow: hidden;
  position: relative;
  z-index: 1;
  display: flex;
  line-height: 30px;
  transition: all 0.4s ease-in-out;
}
.offscreen-navigation ul li > a .overlay {
  display: block;
  position: absolute;
  width: 100%;
  height: 0%;
  top: auto;
  left: 0%;
  bottom: 0%;
  background-color: var(--color-primary);
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease-in-out;
}
.offscreen-navigation ul li > a:hover .overlay {
  height: 100%;
  top: 0%;
  bottom: auto;
  visibility: visible;
  opacity: 1;
}

.offscreen-navigation ul li.menu-item-has-children > a:after {
  color: var(--color-black);
  content: "\f067";
  display: inline-block;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  right: 20px;
  position: absolute;
  top: 50%;
  font-size: 16px;
  opacity: 1;
  visibility: visible;
  transform: translateY(-50%) scale(1);
  transition: 500ms;
}

.offscreen-navigation ul li.menu-item-has-children > a:before {
  color: var(--color-black);
  content: "\f068";
  display: inline-block;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
  right: 20px;
  position: absolute;
  font-size: 16px;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
  transform: translateY(-50%) scale(0);
  transition: 500ms;
  top: 50%;
}

.offscreen-navigation ul li.menu-item-has-children > a.opened:after {
  opacity: 0;
  visibility: hidden;
  transform: translateY(-50%) scale(0);
}

.offscreen-navigation ul li.menu-item-has-children > a.opened:before {
  opacity: 1;
  visibility: visible;
  transform: translateY(-50%) scale(1);
}

